import React from 'react';
import { Route, Switch, BrowserRouter as Router } from 'react-router-dom';
import Landing from './components/landing';
import NewPage from './components/newPage';
import Headers from './components/Headers.js';
import Heropane from './components/Heropane';

function App(props) {
  console.log("IN APPP", props);

  const [activatedRes, setactivatedRes] = React.useState('Nothing');

  function activateFn(val){
    // console.log("RECEIVED A VAL", val)
    setactivatedRes(val);
    props.reachTop(val);
    setTimeout(window.location.href = "/new", 10000);
  }

  return (
    <div className="app">
       <Router>
        <Switch>
            <Route exact path = "/">
              <Headers />
              <Heropane />
            </Route>
          
            <Route path = "/about"  > 
              <Landing usr = {props.username} name="Nidhi" activateFn = {activateFn} />
            </Route>

           <Route path = "/new">
             <NewPage passedInfo = { props.username } passedRes = {activatedRes} passTop = {props.fromTop} />
           </Route>

           
        </Switch>
      </Router> 
    </div>
  );
}

export default App;
